@import "../App/vars";

.tools-card {
  width: 392px;
  padding: 33px;
  border-radius: 15px;
  transition: 0.7s;

  &:hover {
    background: rgba(40, 48, 54, 0.6);
  }

  &:hover .tools-btn {
    background: $orange;
  }

  &:hover .tools-price{
    color: #FFA537;
  }
}

.tools-card__row {
  display: flex;
  gap: 30px;
}

.tools-name {
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  margin-bottom: 3px;
}

.tools-price {
  color: rgba(255, 165, 55, 0.55);
  transition: 0.7s;
}

.tools-text {
  font-size: 16px;
  line-height: 30px;
  margin-top: 30px;
  color: $grey;
}

.tools-card__group {
  display: flex;
  margin-top: 30px;
  justify-content: space-between;
  align-items: center;
}

.tools-card__icons {
  display: flex;
  gap: 18px;
}

.icon-wrapper {
}

.heart-icon path{
  transition: 0.3s;
  cursor: pointer;
  &:hover {
    fill: #FD3B41;
  }
}

.tools-btn {
  text-decoration: none;
  background: #1E252B;
  transition: 0.7s;
  padding: 9px 35px;
}

.tools-card--min {
  width: 288px;
  padding: 28px;
}