@import "../App/vars";

.tools-title {
  margin-bottom: 25px;
}

.tools-subtitle {

}

.tools-cards {
  margin-top: 90px;
  display: flex;
  flex-wrap: wrap;
  row-gap: 100px;
}

.load-more {
  color: $orange;
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 17px 38px;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  background: #1E252B;
  display: block;
  margin: 100px auto 0 auto;
  cursor: pointer;
}