@import "../App/vars";

.about-swiper-slide {
  display: flex;
  gap: 60px;
  align-items: center;
  position: relative;
  justify-content: center;
  left: 50%;
  transform: translateX(-50%);
}

.about-swiper__img {
}

.about-swiper-slide__text {
  font-family: 'Roboto', serif;
  font-size: 14px;
  line-height: 30px;
  color: $grey;
  max-width: 400px;
  position: relative;
  
  span {
    position: absolute;
    top: -100px;
    left: -80px;
    font-family: 'Lora', serif;
    font-weight: 500;
    font-size: 250px;
    line-height: 320px;
    background: linear-gradient(188.88deg, rgba(252, 252, 252, 0) 20.34%, rgba(255, 255, 255, 0.5) 155.72%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    transform: rotate(19.17deg);
  }
}