@import "../App/vars";

.form-box {
  padding: 150px 0;
}

.form-box__wrapper {
  max-width: 560px;
  margin: 0 auto;
}

.form-box__title {
  margin-bottom: 25px;
}

.form-box__subtitle {
  max-width: 427px;
  margin: 0 auto 100px auto;
}

.subtitle {
}

.form {
  max-width: 500px;
  margin: 0 auto;
  position: relative;
}

.form-inp {
  width: 500px;
  height: 64px;
  background: #283036;
  border-radius: 15px;
  border: none;
  outline: none;
  font-size: 14px;
  line-height: 21px;
  color: $grey;
  padding: 21px 55px;
}

.form-btn {
  font-size: $text-small;
  line-height: 24px;
  padding: 12px 36px;
  position: absolute;
  top: 8px;
  right: 12px;
  background: $orange;
  border: none;
  color: #FFF;
  font-weight: 600;
  border-radius: 8px;
  cursor: pointer;
}