@import "../App/vars";

.footer {
  padding: 70px 0 100px 0;
  border-top: 0.5px solid rgba(255, 255, 255, 0.15);
}

.footer-container {
  display: flex;
  justify-content: space-between;
}

.footer-copyright {
  max-width: 260px;
}

.footer-copyright__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  margin: 25px 0px;
}

.footer-copyright__subtitle {
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
}

.footer-row {
  display: flex;
  gap: 115px;
}

.footer-col {
}

.footer-title {
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 20px;
}

.footer-col-list {
  li {
    margin-bottom: 0px;

    a {
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      color: $grey;
      text-decoration: none;
      transition: 0.4s;

      &:hover {
        color: #FFF;
      }
    }
  }
}