@import "./../App/vars";

.navbar-top {
  padding: 25px 0;
}

.navbar-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-list {
  display: flex;
  align-items: center;
  gap: 25px;
}

.navbar-list__item {
  color: $grey;
  font-size: 16px;
  line-height: 24px;
  text-decoration: none;
  transition: 0.3s;
  font-weight: 500;

  &:hover {
    color: #FFF;
  }
}

.navbar-buttons {
  display: flex;
  align-items: center;
  gap: 41px;
}

.login-in {
  color: $grey;
  font-size: $text-small;
  text-decoration: none;
  transition: 0.3s;
  font-weight: 600;

  &:hover {
    color: #FFF;
  }
}

.navbar__sign-up {
  color: #FFF;
  text-decoration: none;
  background: $orange;
  padding: 12px 28px;
  border-radius: 8px;
  line-height: 24px;
  font-size: $text-small;
  font-weight: 600;
}