@import "./../App/vars";

.header {
  padding-top: 200px;
  position: relative;
}

.header-title {
  font-size: 50px;
  font-weight: 500;
  max-width: 600px;
  line-height: 60px;
}

.header-subtitle {
  margin-top: 30px;
  max-width: 490px;
}

.header-search {
  position: relative;
  margin-top: 60px;
  display: inline-block;
}

.search-icon {
  position: absolute;
  top: 20px;
  left: 18px;
  width: 24.08px;
  height: 24px;
}

.search-inp {
  width: 500px;
  height: 64px;
  background: #283036;
  border-radius: 15px;
  border: none;
  outline: none;
  font-size: 14px;
  line-height: 21px;
  color: $grey;
  padding: 21px 55px;
}

.search-btn {
  font-size: $text-small;
  line-height: 24px;
  padding: 12px 36px;
  position: absolute;
  top: 8px;
  right: 12px;
  background: $orange;
  border: none;
  color: #FFF;
  font-weight: 600;
  border-radius: 8px;
  cursor: pointer;
}

.social-icons {
  margin-top: 110px;
}

.header-page {
  z-index: -1;
  position: absolute;
  top: 0;
  right: 0;
}