@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lora:wght@500&display=swap');
@import "vars";

//font-family: 'Roboto', sans-serif;
//font-family: 'Poppins', sans-serif;

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  font-size: 18px;
  line-height: 30px;
  background: #1E252B;
  color: #FFF;
}

.container {
  max-width: 1225px;
  padding: 0 20px;
  margin: 0 auto;
}

.subtitle {
  font-size: 18px;
  line-height: 30px;
  color: $grey;
}

.main-btn {
  font-size: $text-small;
  line-height: 24px;
  padding: 12px 36px;
  background: $orange;
  border: none;
  color: #FFF;
  font-weight: 600;
  border-radius: 8px;
  cursor: pointer;
  text-decoration: none;
}

.section-padding {
  padding: 100px 0;
}

.section-title {
  font-weight: 500;
  font-size: 48px;
  line-height: 55px;
}

.text-center {
  text-align: center;
}